<template>
  <div class="bg">
  <b-container fluid style="padding-top:1vh">
    <b-row>
      <b-col>
        <b-card class="opacity-75 scrollbar">
          <b-card-title>
            Ingredients You Have <b-button variant="danger" v-on:click="removeAll()">Remove All</b-button>
          </b-card-title>
          <b-card-text v-for="(item, i) in useritems" v-bind:key="i">
            {{toTitleCase(item)}} <b-icon style="cursor: pointer; right: 10px; float: right" v-on:click="remove(i)" icon="x" variant="danger" class="float-right"></b-icon>
          </b-card-text>
        </b-card>
      </b-col>
      <b-col>
        <input placeholder="Type ingredients in your inventory..." class="opacity-75" type="text" v-model="input" @keyup.enter="add()">
      </b-col>
      <b-col>
        <b-card class="opacity-75 scrollbar">
          <b-card-title>
            Potential Available Recipes <b-link to="recipes"><b-button variant="info">Recipe List</b-button></b-link>
          </b-card-title>
          <b-card-text v-for="(recipe, i) in allowed" :key="i">{{toTitleCase(recipe.item)}}</b-card-text>
          <b-card-footer v-if="allowed.length > 0">
            <b-badge variant="danger" class="text-black">NOTICE</b-badge>
            You may need multiple of some of these ingredients. Please refer to any reputable cookbook for the recipe and to make sure you have the right amount. We recommend "Cooking & Crafting" by Georgia Banks. Send a telegram to Georgia Banks, Raven McCoy, or Finley McCoy to purchase it for $25.
          </b-card-footer>
        </b-card>
      </b-col>
    </b-row>
    <div style="bottom:1vh" class="footer">
      Created and maintained by Indecision#7334
    </div>
  </b-container>
  </div>
</template>

<script>
import recipes from '../../public/recipes.js'

export default {
  name: 'App',
  data () {
    return {
      input: '',
      useritems: [],
      recipes: recipes,
      allowed: []
    }
  },
  created() {
    this.useritems = JSON.parse(localStorage.getItem('items')) || []
    this.getRecipes()
  },
  watch: {
    useritems: function(val) {
      localStorage.setItem('items', JSON.stringify(val))
      this.getRecipes();
    }
  },
  methods: {
    removeAll() {
      this.useritems = []
    },
    remove(i) {
      this.useritems.splice(i, 1)
    },
    add() {
      if (this.input.length > 0) {
        this.useritems.push(this.input.toLowerCase().trim())
        this.input = ''
      }
    },
    getRecipes() {
      this.allowed = []
      for (let recipe of this.recipes) {
        let allowed = true
        for (let ingredient of recipe.ingredients) {
          if (this.useritems.indexOf(ingredient) === -1) {
            allowed = false
          }
        }
        if (allowed) {
          this.allowed.push(recipe)
        }

      }
    },
    toTitleCase(str) {
      return str.toLowerCase().split(' ').map(function (word) {
        return (word.charAt(0).toUpperCase() + word.slice(1));
      }).join(' ');
    }
  }
}
</script>

<style scoped>
input {
  width: 100%;
  border-radius: 1vh;
  text-align: center;
}

.scrollbar {
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
}

.footer {
  position:absolute;
  bottom: 1vh;
}

.bg {
  background-image: url('../../public/img/paperbg.jpg');
  background-size: cover;
  height: 100vh;
  width: 100vw;
  font-family: 'JMH Typewriter',serif;
}
</style>
