<template>
  <div class="bg">
    <b-container>
      <b-card class="opacity-75 scrollbar">
        <b-card-title>Recipe List <b-link to="/"><b-button variant="info">Back to Recipe Finder</b-button></b-link></b-card-title>
        <b-card-body>
          <b-row v-for="(recipe, i) in getRecipes()" :key="i">
            <b-col cols="8">{{toTitleCase(recipe.string)}}</b-col>
            <b-col cols="1"><b-icon-arrow-right></b-icon-arrow-right></b-col>
            <b-col cols="2">{{toTitleCase(recipe.item)}}</b-col>
          </b-row>
        </b-card-body>
      </b-card>
    </b-container>
  </div>
</template>

<script>
import recipes from '../../public/recipes.js'

export default {
  name: "Recipes",
  methods: {
    getRecipes() {
      let final = []
      for (const i in recipes) {
        const recipe = recipes[i]
        let string = ''
        for (const i in recipe.ingredients) {
          const ingredient = recipe.ingredients[i]
          string += '  ' + ingredient + '  +'
        }
        string = string.slice(0, -1)
        final.push({
          item: recipe.item,
          string: string
        })

      }
      return final
    },
    toTitleCase(str) {
      return str.toLowerCase().split(' ').map(function (word) {
        return (word.charAt(0).toUpperCase() + word.slice(1));
      }).join(' ');
    }
  }
}
</script>

<style scoped>
.bg {
  background-image: url('../../public/img/paperbg.jpg');
  background-size: cover;
  height: 100vh;
  width: 100vw;
  font-family: 'JMH Typewriter',serif;
  padding-top: 1vh
}

.scrollbar {
  width: 100%;
  max-height: 98vh;
  overflow-y: auto;
}
</style>
